#wrapper {
  position: relative;
  width: 100%;
}

main {
  position: relative;
}

main#main {
  overflow: hidden;
}

/* input */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea {
  position: relative;
  background-color: #FFF;
  border: 1px solid #999999;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 0.8rem 1.6rem;
  width: 100%;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select {
  height: 4.5rem;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #888888;
}

input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="tel"]::placeholder,
input[type="number"]::placeholder,
input[type="password"]::placeholder,
select::placeholder,
textarea::placeholder {
  color: #ACACAC;
}

select {
  position: relative;
  appearance: none;
  background-image: url(../img/select-down.svg);
  background-position: calc(100% - 0.8rem) 50%;
  background-size: 1.6rem 1.6rem;
  background-repeat: no-repeat;
  z-index: 0;
}

/* checkbox */
.checkbox {
  display: block;
  position: relative;
  padding-left: 3.2rem;
  cursor: pointer;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 3.0rem;
  text-align: left;
  color: #000000;
  user-select: none;
}

@media only screen and (max-width: 768px) {
  .checkbox {
    font-size: 1.6rem;
    line-height: 2.8rem;
    padding-left: 3.0rem;
  }
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox .checkmark {
  position: absolute;
  top: 0.3rem;
  left: 0;
  height: 2.4rem;
  width: 2.4rem;
  background-color: #FFFFFF;
  border-radius: 0.4rem;
  border: 2px solid #1B1B1F;
}

@media only screen and (max-width: 768px) {
  .checkbox .checkmark {
    top: 0.4rem;
    width: 2.0rem;
    height: 2.0rem;
    border-radius: 0.4rem;
  }
}

.checkbox input:focus ~ .checkmark {
  outline: 0.3rem solid #7EBF41;
}

.checkbox input:checked ~ .checkmark {
  background-color: #3B8E65;
  border-color: #3B8E65;
  outline: none;
}

.checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox input:checked ~ .checkmark:after {
  display: block;
}

.checkbox .checkmark:after {
  left: 0.5rem;
  top: 0;
  width: 0.9rem;
  height: 1.5rem;
  border: solid #FFFFFF;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

@media only screen and (max-width: 768px) {
  .checkbox .checkmark:after {
    left: 0.4rem;
    top: 0;
    width: 0.8rem;
    height: 1.2rem;
  }
}

picture,
figure,
.image_wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

picture img,
figure img,
.image_wrap img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}

.scroll {
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: auto;
}

@media only screen and (max-width: 768px) {
  .scroll {
    padding-bottom: 1.2rem;
  }
  .scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background-color: #f5f5f5;
    border-radius: 0.6rem;
  }
  .scroll::-webkit-scrollbar {
    height: 0.6rem;
    background-color: #f5f5f5;
  }
  .scroll::-webkit-scrollbar-thumb {
    border-radius: 0.6rem;
    background-color: #FED517;
  }
}

.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  max-width: 100%;
  padding: 0.8rem 1.2rem;
  background-color: #3B8E65;
  font-family: "M PLUS Rounded 1c", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: 0.2em;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  border-radius: 2.5rem;
  cursor: pointer;
  z-index: 0;
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  border-radius: inherit;
  border: 2px solid #3B8E65;
  z-index: 2;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.btn:hover {
  color: #3B8E65;
}

.btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.btn span {
  position: relative;
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
  z-index: 3;
}

.btn.btn-yellow {
  background: #ffc520;
  color: #FFF;
  border-radius: 5px;
}

.btn.btn-yellow::after {
  border: unset;
}

.btn.btn-yellow::before {
  background: #7EBF41;
}

@media only screen and (max-width: 768px) {
  .btn {
    padding: 1.3rem 6.0rem 1.3rem 3.2rem;
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: 0.1em;
    border-radius: 2.5rem;
  }
  .btn::after {
    border-width: 2px;
  }
}

