.news-list {
  position: relative;
  width: 100%;
  display: block;
}

.news-list .news-item {
  position: relative;
  width: 100%;
}

.news-list .news-item:last-child a {
  border-bottom: 1px solid #CACACA;
}

.news-list .news-item a {
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
  padding: 2.4rem 0;
  border-top: 1px solid #CACACA;
}

.news-list .news-item a .date {
  width: 16%;
  font-family: "Montserrat";
  color: #ADADAD;
  flex-shrink: 0;
}

.news-list .news-item a .category-wrapper {
  width: 17%;
  flex-shrink: 0;
}

.news-list .news-item a .title {
  font-family: "Noto Sans CJK JP";
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.category {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.4rem 1.6rem;
  width: fit-content;
  height: 2.4rem;
  color: #FFF;
  background: #999999;
  border-radius: 90px;
}

.report-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0px, 1fr));
  column-gap: 4.4rem;
  row-gap: 2.6rem;
}

.report-list .report-item {
  position: relative;
  width: 100%;
  display: block;
}

.report-list .report-item a {
  position: relative;
  width: 100%;
  display: block;
}

.report-list .report-item a .thumb {
  display: block;
  width: 100%;
  height: auto;
  aspect-ratio: 370 / 210;
  object-fit: cover;
  margin-bottom: 2.2rem;
}

.report-list .report-item a .title {
  line-height: 2.2rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

.base-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0px, 1fr));
  column-gap: 2.6rem;
  row-gap: 2.6rem;
}

.base-list .base-item .category {
  margin-bottom: 2.0rem;
}

.base-list .base-item .address {
  line-height: 2.4rem;
}

.map-btn {
  display: inline;
  margin-left: 1.0rem;
  color: #0066CC;
  text-decoration: underline;
}

.spinner {
  width: 2.6rem;
  height: 2.6rem;
  border: 0.5rem dotted #FFF;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 2s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.load-more {
  position: relative;
  width: fit-content;
  height: 4.0rem;
  padding: 0.8rem 7.2rem;
  display: flex;
  column-gap: 1.6rem;
  align-items: center;
  justify-content: center;
  border-radius: 90px;
  background: #999999;
  font-family: "Noto Sans JP";
  font-size: 1.7rem;
  color: #FFF;
  margin: 7.5rem auto 5.4rem auto;
}

.load-more .spinner-wrapper {
  opacity: 0;
  position: absolute;
  width: 2.6rem;
  height: 2.6rem;
  top: 50%;
  left: 2.4rem;
  transform: translateY(-50%);
  transition: opacity 0.2s;
}

.custom-table {
  width: 100%;
  position: relative;
  display: grid;
  grid-template-columns: repeat(1, minmax(0px, 1fr));
  row-gap: 0.4rem;
  margin-bottom: 3.2rem;
}

.custom-table .record {
  width: 100%;
  position: relative;
  display: flex;
  column-gap: 0.4rem;
}

.custom-table .record .th {
  width: 12%;
  flex-shrink: 0;
  color: #FFF;
  background: #B7D100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.custom-table .record .td {
  width: 100%;
  padding: 1.6rem;
  line-height: 3.2rem;
  border: 1px solid #ADADAD;
}

.form-section {
  padding-top: 5.6rem;
}

.form-section form ul {
  margin: 0 auto;
  max-width: 56rem;
}

.form-section form .bottom-desc {
  width: fit-content;
  margin: 4.0rem auto 0 auto;
  text-align: left;
  line-height: 2.8rem;
}

.main-form {
  margin-bottom: 5.4rem;
}

.main-form .form-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;
}

.main-form .vertical-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 1.6rem;
}

.main-form .horizontal-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 1.6rem;
}

.main-form .input-privacy {
  width: fit-content;
  margin: 1rem auto 0 auto;
}

.main-form .input-privacy input {
  margin-right: 0.8rem !important;
}

.main-form .input-privacy a {
  text-decoration: underline;
}

.main-form .input-privacy a:hover {
  opacity: 0.7;
}

.main-form .input-privacy label {
  display: flex;
  align-items: center;
}

.main-form .checkbox-item {
  -webkit-row-gap: 2.4rem;
  -moz-row-gap: 2.4rem;
  row-gap: 2.4rem;
}

.main-form textarea {
  line-height: 2.4rem;
}

.main-form .btn {
  padding: 2.3rem 6.4rem;
  height: 6.4rem;
  width: fit-content;
}

.main-form .btn-black::before {
  background: #2175D6;
}

.main-form .btn-black:hover span {
  color: #FFF;
}

.main-form .btns-wrapper {
  margin: 4.8rem auto 0 auto;
}

.main-form.main-confirm input,
.main-form.main-confirm textarea {
  padding: 0;
  border: unset;
  font-weight: 400;
}

.main-form.main-confirm .name-wrapper .horizontal-wrapper {
  justify-content: flex-start;
}

.main-form.main-confirm .name-wrapper .horizontal-wrapper input {
  width: 3.4rem;
}

.main-form .required {
  width: fit-content;
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 0.8rem;
}

.main-form .required::after {
  width: fit-content;
  content: '';
  color: #F00;
  line-height: 100%;
}

.main-form input, .main-form textarea {
  font-size: 1.8rem;
  font-weight: 500;
}

.main-form .btn-submit {
  position: relative;
  width: fit-content;
  height: 4.0rem;
  padding: 0 7.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFF;
  background: #999999;
  border-radius: 90px;
  margin: 1.2rem auto 0 auto;
}

input[type=checkbox] {
  position: relative;
  border-radius: 4px;
  border: 1.5px solid #000;
  background: none;
  cursor: pointer;
  line-height: 0;
  outline: 0;
  padding: 0 !important;
  vertical-align: text-top;
  height: 2rem !important;
  width: 2rem !important;
  -webkit-appearance: none;
  opacity: .5;
  margin: 0 0.8rem 0 0;
}

input[type=checkbox]:hover {
  opacity: 1;
}

input[type=checkbox]:checked {
  background-color: #7EBF41;
  border: 1.5px solid #FFF;
  opacity: 1;
}

input[type=checkbox]:checked:before {
  content: '';
  position: absolute;
  right: calc(50% + 0rem);
  top: 50%;
  width: 0.6rem;
  height: 1.3rem;
  border: solid #FFF;
  border-width: 0 2px 2px 0;
  margin: -1px -1px 0 -1px;
  transform: rotate(45deg) translate(-50%, -50%);
  z-index: 2;
}

.footer {
  padding-top: 6.2rem;
  padding-bottom: 2.4rem;
  position: relative;
  width: 100%;
