  font-size: 1.5rem;
  font-family: "Noto Sans CJK JP";
  color: #333;
}

.footer .logo-nav .nav-wrapper .nav-menu .menu-link.sub-menu {
  font-size: 1.3rem;
  padding-left: 1.5rem;
  margin: 0 0 0.8rem 0;
}

.footer .copyright {
  width: fit-content;
  color: #ADADAD;
  margin: 0 auto;
}
